.auth-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  margin-top: 2rem;
}

.auth {
  width: 500px;
  padding: 2rem 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}

.auth__field:not(:last-child) {
  margin-bottom: 1rem;
}

.auth__input {
  width: 200px;
  padding: 5px;
}

.field {
  display: flex;
  flex-direction: column;
}

.error {
  border: 1px solid red !important;
  color: red;
}

.auth__item {
  position: relative;
}

.err-msg {
  position: absolute;
  top: 60px;
  left: 10;
  color: red;
}

input {
  width: 295px;
  height: 60px;
  border-radius: 5px;
}
